<template>
    <div class="navbar" @keyup.esc="this.burger = false">
        <h2 @click="$router.push('/vue/')">Vue - 3</h2>
        <my-button
            class="link btn"
            v-if="widthWindow < 700"
            @click="closeBurger"
            >Menu</my-button
        >
        <div
            v-if="burger || widthWindow > 700"
            class="links"
            @click.stop="this.burger = false"
        >
            <my-button
                class="link"
                @click="$router.push('/vue/posts')"
                style="color: black; border-radius: 10px"
                >Posts</my-button
            >
            <my-button
                class="link"
                @click="$router.push('/vue/about')"
                style="color: black; border-radius: 10px"
                >About</my-button
            >
            <my-button
                class="link"
                @click="$router.push('/vue/store')"
                style="color: black; border-radius: 10px"
                >Post-VueX</my-button
            >
            <my-button
                class="link"
                @click="$router.push('/vue/calc')"
                style="color: black; border-radius: 10px"
                >Custom Calc</my-button
            >
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            burger: false,
            widthWindow: window.innerWidth,
        };
    },
    methods: {
        width() {
            this.widthWindow = window.innerWidth;
        },
        closeBurger() {
            this.burger = !this.burger;
        },
    },
    mounted() {
        window.addEventListener("resize", this.width);
    },

    watch: {
        widthWindow() {
            this.width();
        },
    },
};
</script>
<style>
.navbar {
    background: rgb(172, 209, 209);
    box-shadow: 2px 2px 4px gray;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
}
.navbar h2 {
    cursor: pointer;
}
.links {
    margin-left: auto;
}


@media (max-width: 700px) {
    .navbar {
        justify-content: space-between;
        padding: 10px;
    }
    .links {
        z-index: 10;
        position: absolute;
        left: 0;
        top: 0;
        width: 300px;
        height: 100vh;
        background: rgba(0, 0, 0, 0.5);
        display: flex;
        flex-direction: column;
        gap: 10px;
        justify-content: center;
        align-items: center;
    }
    .links .link {
        z-index: 11;
        background: #fff;
        width: 80%;
    }
}
.link {
    font-size: 20px;
    margin-right: 20px;
}
.btn {
    margin: 0;
}
</style>
