<template>
    <div class="dialog" v-if="show" @click.stop="hideDialog">
        <div @click.stop class="dialog__content"><slot></slot></div>
    </div>
</template>
<script>
import toogleMixin from "@/mixins/toogleMixin";

export default {
    name: "my-dialog",
    mixins: [toogleMixin],
};
</script>
<style>
.dialog {
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
    position: fixed;
    display: flex;
}
.dialog__content {
    margin: auto;
    background-color: white;
    border-radius: 25px;
    min-height: 70px;
    min-width: 300px;
    padding: 20px;
}
</style>
